<script>
import config from "../../../../public/config/customSetting.json";
export default {
  name: "ticketDetailsComponent",
  components: {},
  data() {
    return {
      data: {},
    };
  },
  computed: {},
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    formatDate(data) {
      this.data.violationDate =
        data.violation_Date.split("T")[0].split("-").join("/") +
        " " +
        data.violation_Date.split("T")[1];
      this.data.violationTime =
        data.violation_Time.split("T")[0].split("-").join("/") +
        " " +
        data.violation_Time.split("T")[1];
      this.data.endTime =
        data.end_Time.split("T")[0].split("-").join("/") +
        " " +
        data.end_Time.split("T")[1];
    },
    getFineInfo(fineId) {
      this.$store
        .dispatch("QryFineInfo", fineId)
        .then((res) => {
          const data = res.data.data;
          this.data = data;
          this.formatDate(this.data);
        })
        .catch((err) => console.log(err));
    },
    async downloadHandler(item) {
      const fileName = item.fileName.split(".")[0];
      const fileFormat = item.fileName.split(".")[1];
      const res = await this.$store.dispatch("commonDownloadPdf", {
        file_id: item.filePath,
        router_Name: config.getFineMgmt,
      });
      this.downloadFile(res, fileName, fileFormat);
    },
  },
  mounted() {
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
    const fine_Id = this.$route.params.fine_Id;
    this.getFineInfo(fine_Id);
  },
};
</script>

<template src="./ticketDetails.html"></template>
